





































































































import { Component, Vue } from 'vue-property-decorator'
import { Matrix } from '@/lib/matrix'
import { gaussian_elimination_solve } from '@/lib/gaussian_elimination'

const vmj = require('vue-mathjax')
Vue.use(vmj)

@Component
export default class Gauss extends Vue {
      private data = {
        A: '-2,-2,0\n2,1,1\n4,2,3',
        b: '-1\n2\n1',
        row: 0,
        col: 0,
        formula: '',
        osolution: ''
      }

      private m_A : any

      mounted() {
        this.compute()
      }

      initFormula() {
        let out_formula = '$$\\left\\{\\begin{array}{c}'

        let v_b = this.data.b.split('\n')
        this.data.row = v_b.length
        let m_A_array = this.data.A.split('\n')
        this.data.col = m_A_array[0].split(',').length
        this.m_A = new Matrix(this.data.row, this.data.col + 1)

        for (let i = 0; i < this.data.row; i++) {
          let A_array = m_A_array[i].split(',')

          for (let j = 0; j < this.data.col; j++) {
            let value = +A_array[j]
            this.m_A.setDataByIndexs(i, j, value)

            out_formula += value.toString() + 'x_' + (j + 1).toString()
            if (j === this.data.col - 1) { out_formula += '= &' } else { out_formula += '+' }
          }
          this.m_A.setDataByIndexs(i, this.data.col, +v_b[i])
          out_formula += v_b[i] + '\\\\'
        }
        out_formula += '\\end{array}\\right.$$'
        this.data.formula = out_formula
      }

      compute() {
        this.initFormula()
        let result = gaussian_elimination_solve(this.m_A)
        let out_solution = '$$'
        for (let index = 0; index < result.length; index++) {
          const element = result[index]
          out_solution += 'x_' + (index + 1).toString() + '=' + element + '\\\\'
        }
        out_solution += '$$'
        this.data.osolution = out_solution
      }
}
